export default "<template>\r\n    <div class=\"home\">\r\n        <raw-loader :mytxt=\"require('!raw-loader!./UserClaims.vue').default\"></raw-loader>\r\n        <h1>This is TestNDClaim</h1>\r\n        <b-row>\r\n            <b-col md=\"4\">\r\n                <b-img src=\"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQdVTcxFVJTxqNW7ujs-W2XHHaryQ6_w-OBWw&usqp=CAU\" fluid alt=\"Responsive image\"></b-img>   \r\n            </b-col>\r\n            <b-col md=\"8\">\r\n                <div v-for=\"claimName in claimNames\" :key=\"claimName.Id\">\r\n                    <h3 class=\"bg-light p-2\">Claim Name : [{{ claimName }}]</h3>\r\n                    <NDClaimCom :claimname=\"claimName\"></NDClaimCom>\r\n                </div>\r\n            </b-col>\r\n        </b-row>\r\n    </div>\r\n</template>\r\n<script>\r\nimport fetchClient from '@/fetchClient';\r\nimport TumLum from '@/TumLum';\r\nimport NDClaimCom from \"@/views/NguoiDung/UserClaimsCom.vue\";\r\nexport default {\r\n    components: {\r\n        NDClaimCom\r\n    },\r\n    data(){\r\n        return{\r\n            ds:[],\r\n            url:`/api/NguoiDungClaim4`,\r\n            claimNames:[]\r\n        }\r\n    },\r\n    mounted(){\r\n        /*\r\n        let r= { \r\n            \"Id\": 0, \r\n            \"UId\": 1, \r\n            \"ClaimType\": \"Them\", \r\n            \"ClaimName\": \"Ban\" \r\n        }\r\n        { \"Id\": 1, \"UId\": 1, \"ClaimType\": \"Them\", \"ClaimName\": \"Ban\" }         \r\n        this.Add(r);\r\n        let r= { \r\n            \"Id\": 1, \r\n            \"UId\": 1, \r\n            \"ClaimType\": \"Themsfsf\", \r\n            \"ClaimName\": \"Bansfsf\" \r\n        }\r\n         this.Update(r);     \r\n        */\r\n        //this.Del(1);\r\n        //this.GetAll();\r\n        this.getClaimNames();\r\n        //this.GetListByClaimName('Ban');\r\n    },\r\n    methods: {\r\n        async GetListClaimName() {\r\n            return await fetchClient.get(`${this.url}/GetAll`)\r\n                .then(res => {\r\n                    alert(JSON.stringify(res));\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n        async GetAll() {\r\n            return await fetchClient.get(`${this.url}/GetAll`)\r\n                .then(res => {\r\n                    this.ds=res.data.list;\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n        async Add(r) {\r\n            return await fetchClient.post(`${this.url}/Add`, r)\r\n                .then(res => {\r\n                    this.GetAll();\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n        async Update(r) {\r\n            return await fetchClient.post(`${this.url}/Update`, r)\r\n                .then(res => {\r\n                    this.GetAll();\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n        async Del(id) {\r\n            return await fetchClient.get(`${this.url}/Delete?id=${id}`)\r\n                .then(res => {\r\n                    alert(JSON.stringify(res));\r\n                    this.GetAll();\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n        async getById(id) {\r\n            return await fetchClient.get(`${this.url}/GetById?id=${id}`)\r\n                .then(res => {\r\n                    //alert(JSON.stringify(res));\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n        async getClaimTypes() {\r\n            return await fetchClient.get(`/api/NguoiDung4/GetClaimTypes`)\r\n                .then(res => {\r\n                    //alert(JSON.stringify(res));\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n        async getClaimNames() {\r\n            return await fetchClient.get(`/api/NguoiDung4/GetClaimNames`)\r\n                .then(res => {\r\n                    this.claimNames=res.data;\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n        async GetListByClaimName(claimName) {\r\n            return await fetchClient.get(`/api/NguoiDungClaim4/GetListByClaimName?ClaimName=${claimName}`)\r\n                .then(res => {\r\n                    //alert(JSON.stringify(res));\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n    }\r\n}\r\n</script>\r\n  ";