        let r= { 
            "Id": 0, 
            "UId": 1, 
            "ClaimType": "Them", 
            "ClaimName": "Ban" 
        }
        { "Id": 1, "UId": 1, "ClaimType": "Them", "ClaimName": "Ban" }         
        this.Add(r);
        let r= { 
            "Id": 1, 
